@import './common/style/sass/mixins';

$mediaWidth: 1100px;

.appContainer {
  height: 100vh;
  background-image: url("./assets/img/backGroundImage.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  .letterContainer {
    height: 100%;
    @include flex(column, center, center, wrap);


    .letter {
      width: 700px;
      height: 500px;
      border: 1px solid #4b4b4b;
      background-color: rgba(255, 255, 255, 0.7);
      border-radius: 10px;
      @include flex(column, center, center, wrap);
      @media screen and (max-width: $mediaWidth) {
        width: 80%;
        @include text(14px, 14px, 400);
      }


      .lang {
        @include flex(column, flex-start, flex-start, wrap);
        @include text(24, 2.5, 600);
        @media screen and (max-width: $mediaWidth) {
          padding: 20px;
        }

        .eng, .rus {
          font-weight: 500;
        }
      }

      .behavior {
        height: 100%;
        @include flex(column, center, flex-start, wrap);
        @include text(24, 2.5, 600);

        .answer {
          font-weight: 500;
        }
      }

      .form {
        height: 90%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .underTree {
          @include flex(row, center, center, wrap);
          margin: 3px;
        }
      }

      .finalText {
        padding: 40px;
        @include flex(column, center, flex-start, wrap);
        //background-color: red;
        @include text(24, 2.5, 400);
      }
    }
  }
}


.input {
  width: 300px;
  height: 20px;
  margin: 10px 0;
  padding: 5px 10px;
  border: 0px solid black;
  border-radius: 10px;
  @media screen and (max-width: $mediaWidth) {
    width: 240px;
  }
}

.textArea {
  width: 300px;
  height: 130px;
  margin: 10px 0;
  padding: 5px 10px;
  border: 0px solid black;
  border-radius: 10px;
  resize: none;
  @media screen and (max-width: $mediaWidth) {
    width: 240px;
  }
}

.button {
  width: 320px;
  height: 30px;
  border: 0px solid black;
  border-radius: 10px;
  cursor: pointer;
  @media screen and (max-width: $mediaWidth) {
    width: 260px;
  }
}

a {
  text-decoration: none;
  color: black;
}